.categoriesContent{
  /* margin: 0 auto; */
  display: grid;
}




.categories{
    padding: 1rem;
}



.img{
  width: 360px;
  height: 230px;
}

.title{
  color: var(--title-color);
  font-weight: 700;
  font-size: 13px;
  line-height: 13px;
  margin-top: 10px;
}

.info{
  font-weight: 700;
  line-height: 30px;
  color: var(--black);
  margin-top: 10px;
}
.name{
  margin-top: 20px;
}
.name p span{
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: var(--gray-date);
  margin-top: 10px;
}

.name p span:first-child{
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;
  color: var(--black);
}

.text{
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--gray-text);
  margin-top: 10px;
}

@media (min-width: 600px) {
    .categoriesContent { grid-template-columns: repeat(2, 1fr); }
  }


  @media (min-width: 900px) {
    .categoriesContent { grid-template-columns: repeat(3, 1fr);     padding-left: 60px;}
    
  }