.header{
    width: 100%;
    height: 140px;
    margin: 0 auto;
    padding: 10px 0;
}

.mainHeader{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border: 1px solid var(--gray-date);
    
}


.logo{
    display: flex;
    justify-content: center;
    padding: 20px;
}


.nav{
    display: flex;
    justify-content: center;
}

.nav ul{
    width: 100%;
    display: flex;
    list-style: none;
    border: 1px solid var(--gray-date);
    justify-content: center;
    padding: 20px 0;

}

.nav ul li{
    padding: 2px 10px;
    cursor: pointer;
    display: flex;
    position: relative;
}

.nav ul li a:hover{
    color: red;
    transition: all .3s ease-out;
}

.nav ul li a{
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: var(--black);
}

.nav ul li svg{
    /* transform: rotate(-90deg); */
    margin-left: 5px;
}




.post{
    width: 200px;
    border: 1px solid var(--gray-date);
    position: absolute;
    top: 40px;
    left: 0;
    z-index: 2;
    background-color: var(--white);
    padding: 0 20px;
}

.post ul{
    display: block;
    border: none;
   
}

.post ul li {
    padding: 10px 0;
    border-bottom: 1px solid var(--gray-date);
}
.post ul li a{
    font-weight: 400;
    font-size: 13px;
    line-height: 13px;
}
.post ul li a:hover{
    color: var(--gray-links);
    margin-left: 10px;
    transition: all .3s ease-out;
}

.post ul li:last-child{
    border: none;
}

.post ul li div{
    display: flex;
    justify-content: space-between;
    width: 100%;
}


.search{
    position: absolute;
    right: 30px;
    top: 35px;
    z-index: 10;
    cursor: pointer;
}

.search svg{
    width: 20px;
    height: 20px;
}
/* burger */


.burger{
    display: block;
    height: 30px;
    width: 30px;
    position: absolute;
    top: 18px;
    left: 20px;
    z-index: 20;
    
}

.close{
    height: 30px;
    width: 30px;
    position: absolute;
    top: 18px;
    right: 60px;
    z-index: 20;
    cursor: pointer;
}

.logoResponive{
    position: absolute;
    top: 20px;
    z-index: 1;
}


/* use useState  none display */

.none{
    display: block;
    height: 100vh;

}

.none{
    width: 200px;
    border: 1px solid var(--gray-date);
    position: absolute;
    top: 40px;
    left: 0;
    z-index: 2;
    background-color: var(--white);
    padding: 0 20px;
    margin-top: 50px;

}
/* block items */
.block{
    display: none;
}
.none ul{
    display: block;
    border: none;
   
}

/* none nav  */

.none ul li {
    padding: 10px 0;
    border-bottom: 1px solid var(--gray-date);
}
.none ul li a{
    font-weight: 400;
    font-size: 13px;
    line-height: 13px;
}
.none ul li a:hover{
    color: var(--gray-links);
    margin-left: 10px;
    transition: all .3s ease-out;
}

.none ul li:last-child{
    border: none;
}

.none ul li div{
    display: flex;
    justify-content: space-between;
    width: 100%;
}


.none .post{
        margin-left: 200px;
}





